<template>
	<view class="detail" v-show="!isLoading">
		<view class="fixbg" :style="{'backgroundImage':'url('+songDetail.songs[0].al.picUrl+')'}"></view>
		<musichead :title="songDetail.songs[0].name" :icon="true" color="white"></musichead>
		<view class="container" v-show="!isLoading">
			<scroll-view scroll-y="true">
				<view class="detail-play" @tap="handlePlay">
					<image :src="songDetail.songs[0].al.picUrl" :class="{'detail-play-run' :ImageIsRotate}" mode="">
					</image>
					<text class="iconfont" :class="iconPlay"></text>
					<view></view>
				</view>
				<view class="detail-lyric">
					<view class="detail-lyric-wrap" :style="{'transform':'translateY('+-(lyricIndex-1)*82+'rpx)'}">
						<view class="detail-lyric-item" :class="{'active': lyricIndex === index}"
							v-for="(item,index) in songLyric" :key="index">{{item.lyric}}</view>
					</view>
				</view>
				<view class="detail-like">
					<view class="detail-like-head">喜欢这首歌的人也听</view>
					<view @tap="handleToSimi(item.id)" class="detail-like-item" v-for="item in songSimi" :key="item.id">
						<view class="detail-like-img">
							<image :src="item.album.picUrl"></image>
						</view>
						<view class="detail-like-song">
							<view>{{item.album.name}}</view>
							<view>
								<text v-if="item.privilege.flag >60 && item.privilege.flag < 70 "
									style="color:red">独家</text>
								<text v-if="item.privilege.maxbr === 999000" style="color:red">SQ</text>
								<text>{{item.album.artists[0].name}} - {{item.album.name}}</text>
							</view>
						</view>
						<text class="iconfont icon-bofang_o detail-like-song-text"></text>
					</view>
				</view>
				<view class="detail-comment">
					<view class="detail-comment-head">精彩评论</view>
					<view class="detail-comment-item" v-for="item in songHotComment" :key="item.commentId">
						<view class="detail-comment-img">
							<image :src="item.user.avatarUrl" mode=""></image>
						</view>
						<view class="detail-comment-content">
							<view class="detail-comment-title">
								<view class="detail-comment-name">
									<view>{{item.user.nickname}}</view>
									<view>{{item.time | formatDate}}</view>
								</view>
								<view class="detail-comment-like">{{item.likedCount | formatCount}}<text
										class="iconfont icon-dianzan"></text></view>
							</view>
							<view class="detail-comment-text">
								{{item.content}}
							</view>
						</view>
					</view>
				</view>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	import {
		songDetail,
		songComment,
		songSimi,
		songLyric,
		songUrl
	} from "@/common/api.js"
	export default {
		data() {
			return {
				songDetail: {
					songs: [{
						name:"",
						al: {
							picUrl: ""
						}
					}]
				},
				songSimi: [],
				songHotComment: [],
				songLyric: [],
				lyricIndex: 0,
				iconPlay: "icon-zanting",
				ImageIsRotate: true,
				bgAudioManager: uni.getBackgroundAudioManager(),
				innerAudioContext:uni.createInnerAudioContext(),
				isLoading:false

			}
		},
		onLoad(options) {
			this.getMusic(options.songId);

		},
		onUnload() {
			this.cancelLyricIndex();
			// #ifdef H5
			this.bgAudioManager.pause()
			this.bgAudioManager.destroy();
			// #endif
			
		},
		onHide() {
			this.cancelLyricIndex();
			// #ifdef H5
			this.bgAudioManager.pause()
			this.bgAudioManager.destroy();
			// #endif
		},
		methods: {
			getMusic(songId) {
				this.$store.commit("listSongNext",songId);
				console.log(songId,this.$store.state.nextSongId);
				uni.showLoading({
					title:"加载中"
				})
				this.isLoading = true;
				Promise.all([songDetail(songId), songSimi(songId), songComment(songId), songLyric(songId), songUrl(
					songId)]).then(res => {
					// console.log(res);
					if (res[0][1].statusCode === 200) {
						this.songDetail = res[0][1].data;
					}
					if (res[1][1].statusCode === 200) {
						this.songSimi = res[1][1].data.songs;
					}
					if (res[2][1].statusCode === 200) {
						this.songHotComment = res[2][1].data.hotComments;

					}
					if (res[3][1].statusCode === 200) {
						const lyric = res[3][1].data.lrc.lyric;
						const re = /\[([^\]]+)\]([^\[]+)/g;
						const arr = []
						lyric.replace(re, ($0, $1, $2) => {
							arr.push({
								time: this.FormatTimeToSec($1),
								lyric: $2
							})
						});
						this.songLyric = arr;
					}
					if (res[4][1].statusCode === 200) {
						// #ifdef MP-WEIXIN
						this.bgAudioManager = uni.getBackgroundAudioManager();
						this.bgAudioManager.title = this.songDetail.songs[0].name;
						// #endif
						
						// #ifdef H5
						this.bgAudioManager = uni.createInnerAudioContext();					
						this.iconPlay = "icon-bofang";
						this.ImageIsRotate = false;
						// #endif
					
						this.bgAudioManager.src = res[4][1].data.data[0].url || "";
						this.listenLyricIndex();
						this.bgAudioManager.onPlay(() => {
							this.iconPlay = "icon-zanting";
							this.ImageIsRotate = true;
							this.listenLyricIndex();
						})
						this.bgAudioManager.onPause(() => {
							this.iconPlay = "icon-bofang";
							this.ImageIsRotate = false;
							this.cancelLyricIndex();
						})
						this.bgAudioManager.onEnded(()=>{
							this.getMusic(this.$store.state.nextSongId)
						})
					}				
					uni.hideLoading();
					this.isLoading = false;
				})
			},
			// 时间转化成秒
			FormatTimeToSec(val) {
				const str = val.split(":");
				const sec = parseFloat(str[0]) * 60;
				const result = sec + Number(parseFloat(str[1]).toFixed(1));
				return result;
			},
			handlePlay() {
				if (this.bgAudioManager.paused) {
					this.bgAudioManager.play();
				} else {
					this.bgAudioManager.pause();
				}
			},
			listenLyricIndex() {
				clearInterval(this.timer);
				this.timer = setInterval(() => {
					let curTime = this.bgAudioManager.currentTime
					for (let i = 0; i < this.songLyric.length; i++) {
						if (curTime > this.songLyric[this.songLyric.length - 1].time) {
							this.lyricIndex = this.songLyric.length - 1;
							break;
						}
						if (curTime > this.songLyric[i].time && curTime < this.songLyric[i + 1].time) {
							this.lyricIndex = i;
						}
					}	
				}, 300)
				// this.timer = setInterval(()=>{
				// 	for(let i = 0;i<this.songLyric.length;i++){
				// 		if(this.bgAudioManager.currentTime > this.songLyric[this.songLyric.length-1].time){
				// 			this.lyricIndex = this.songLyric.length-1;
				// 			break;
				// 		}
				// 		if(this.bgAudioManager.currentTime > this.songLyric[i].time && this.bgAudioManager.currentTime < this.songLyric[i+1].time){
				// 			this.lyricIndex = i;
				// 		}
				// 		console.log(i);
				// 	}
				// 	console.log(this.lyricIndex);
				// },550)
			},
			cancelLyricIndex() {
				clearInterval(this.timer)
			},
			handleToSimi(id){
				// 解决H5歌曲叠唱
				// #ifdef H5
				this.bgAudioManager.destroy();
				// #endif
				this.getMusic(id);
			}
		}
	}
</script>

<style scoped>
	.detail-play {
		position: relative;
		width: 540rpx;
		height: 540rpx;
		background: url(https://leo-1310014300.cos.ap-guangzhou.myqcloud.com/disc.png);
		background-size: cover;
		margin: 214rpx auto 0 auto;
	}

	.detail-play image {
		width: 370rpx;
		height: 370rpx;
		border-radius: 50%;
		position: absolute;
		top: 0;
		right: 0;
		left: 0;
		bottom: 0;
		margin: auto;
		animation: 10s linear move infinite;
		animation-play-state: paused;
	}

	.detail-play .detail-play-run {
		animation-play-state: running;
	}

	@keyframes move {
		from {
			transform: rotate(0deg);
		}

		to {
			transform: rotate(360deg);
		}
	}

	.detail-play text {
		width: 100rpx;
		height: 100rpx;
		font-size: 100rpx;
		color: white;
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		margin: auto;
	}

	.detail-play view {
		width: 500rpx;
		height: 405rpx;
		background: url(https://leo-1310014300.cos.ap-guangzhou.myqcloud.com/needle.png);
		position: absolute;
		left: 80rpx;
		right: 0;
		top: -250rpx;
		margin: auto;
		background-size: cover;
		transform: scale(0.5) rotate(20deg);
	}

	.detail-lyric {
		margin-top: 30rpx;
		font-size: 32rpx;
		line-height: 82rpx;
		height: 246rpx;
		text-align: center;
		overflow: hidden;
		color: #6f6e73;
	}

	.detail-lyric-wrap {
		transition: 0.2s;
	}

	.detail-lyric-item {
		height: 82rpx;
	}

	.detail-lyric-item.active {
		color: white
	}

	.detail-like {
		margin: 0 30rpx;
	}

	.detail-like-head {
		font-size: 36rpx;
		color: white;
		margin: 50rpx 0;
	}

	.detail-like-item {
		display: flex;
		align-items: center;
		margin-bottom: 28rpx;
	}

	.detail-like-img {
		width: 82rpx;
		height: 82rpx;
		border-radius: 20rpx;
		overflow: hidden;
		margin-right: 20rpx;
	}

	.detail-like-img image {
		width: 100%;
		height: 100%;
	}

	.detail-like-song {
		flex: 1;
		color: #c6c2bf;
	}

	.detail-like-song view:nth-child(1) {
		font-size: 28rpx;
		color: white;
		margin-bottom: 12rpx;
	}

	.detail-like-song view:nth-child(2) {
		font-size: 22rpx;
	}

	.detail-like-song image {
		width: 26rpx;
		height: 20rpx;
		margin-right: 10rpx;
	}

	.detail-like-song-text {
		font-size: 50rpx;
		color: #c6c2bf;
	}

	.detail-comment {
		margin: 0 30rpx;
	}

	.detail-comment-head {
		font-size: 36rpx;
		color: white;
		margin: 50rpx 0;
	}

	.detail-comment-item {
		margin-bottom: 28rpx;
		display: flex;
	}

	.detail-comment-img {
		width: 64rpx;
		height: 64rpx;
		border-radius: 50%;
		overflow: hidden;
		margin-right: 18rpx;
	}

	.detail-comment-img image {
		width: 100%;
		height: 100%;
	}

	.detail-comment-content {
		flex: 1;
		color: #cbcacf
	}

	.detail-comment-title {
		display: flex;
		justify-content: space-between;
	}

	.detail-comment-name {}

	.detail-comment-name view:nth-child(1) {
		font-size: 26rpx;
	}

	.detail-comment-name view:nth-child(2) {
		font-size: 20rpx;
	}

	.detail-comment-like {
		font-size: 28rpx;
	}

	.detail-comment-text {
		font-size: 28rpx;
		line-height: 40rpx;
		color: white;
		margin-top: 20rpx;
		border-bottom: 1px solid #59595b;
		padding-bottom: 40rpx;
	}
</style>
